import React from "react";
import style from "./Home.module.css";
import mainImg from "../../utils/mainImg.png";
import { LazyLoadImage } from "react-lazy-load-image-component";
const Home = () => {
  return (
    <div className={style.container}>
      <LazyLoadImage
        className={style.bgImage}
        src={mainImg}
        placeholder={<span>loading</span>}
        style={{
          width: "100%",
          height: "100%",
        }}
      ></LazyLoadImage>

      <div className={style.bgtext}>
        <label className={style.headertext}>
          WE ARE COMMITED TO YOUR HEALTH
        </label>
        <label className={style.subtext}>We are available 24/7</label>
        <div className={style.groupbuttons}>
          <div
            className={style.button}
            onClick={() => {
              window.location.href = "?type=1#about";
            }}
          >
            Book Appointment
          </div>
          <div
            className={style.button1}
            onClick={() => {
              window.location.href = "?type=2#about";
            }}
          >
            Book HomeService
          </div>
        </div>
      </div>
    </div>
  );
};

export default Home;
