import React, { useState, useEffect } from "react";
import style from "./About.module.css";
import contactImg from "../../utils/contact.png";
const About = ({ location }) => {
  const [payload, setPayload] = useState({
    first_name: "",
    last_name: "",
    email: "",
    phone: "",
    notes: "",
    service: "1",
  });
  const [errorObj, setErrorObj] = useState({
    first_name: null,
    phone: null,
  });
  let params = new URLSearchParams(location.search);

  const handleUserInput = (key, value) => {
    setPayload(Object.assign({}, payload, { [key]: value }));
  };
  useEffect(() => {
    let serviceType = params.get("type");
    if (serviceType != null && serviceType != undefined) {
      setPayload(Object.assign({}, payload, { service: serviceType }));
    }
  }, [params.get("type")]);
  const formValidator = () => {
    let flag = true;
    let error = {
      first_name: null,
      phone: null,
    };
    if (payload.first_name.trim().length < 3) {
      error.first_name = "Please Enter a valid first name";
      flag = false;
    }
    if (payload.phone.trim().length != 10) {
      error.phone = "Please Enter a valid phone number";
      flag = false;
    }
    setErrorObj(Object.assign({}, error));
    return flag;
  };
  const handleSubmit = () => {
    if (formValidator()) {
      fetch("/sendmail", {
        method: "post",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload),
      })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };
  return (
    <div className={style.container}>
      <img src={contactImg} className={style.aboutimg} />
      <div className={style.form}>
        <h1>Fill your details</h1>
        <div className={style.field}>
          <label>First Name</label>
          <input
            type="text"
            value={payload.first_name}
            onChange={(e) => {
              handleUserInput("first_name", e.currentTarget.value);
            }}
            className={errorObj.first_name == null ? "" : style["input--error"]}
          />
          {errorObj.first_name && (
            <span className={style.errortext}>{errorObj.first_name}</span>
          )}
        </div>
        <div className={style.field}>
          <label>Last Name</label>
          <input
            type="text"
            value={payload.last_name}
            onChange={(e) => {
              handleUserInput("last_name", e.currentTarget.value);
            }}
            required={true}
          />
        </div>
        <div className={style.field}>
          <label>Email</label>
          <input
            type="text"
            value={payload.email}
            onChange={(e) => {
              handleUserInput("email", e.currentTarget.value);
            }}
          />
        </div>
        <div className={style.field}>
          <label>Phone</label>
          <input
            type="text"
            value={payload.phone}
            onChange={(e) => {
              handleUserInput("phone", e.currentTarget.value);
            }}
            className={errorObj.phone == null ? "" : style["input--error"]}
          />
          {errorObj.phone && (
            <span className={style.errortext}>{errorObj.phone}</span>
          )}
        </div>
        <div className={style.field}>
          <label>Additional Info</label>
          <input
            type="text"
            value={payload.notes}
            onChange={(e) => {
              handleUserInput("notes", e.currentTarget.value);
            }}
          />
        </div>
        <div className={style.field}>
          <label>Service</label>
          <select
            onChange={(e) => {
              handleUserInput("service", e.currentTarget.value);
            }}
            value={payload.service}
          >
            <option value="1">Consulting</option>
            <option value="2">Home Service</option>
          </select>
        </div>
        <div className={style.button} onClick={handleSubmit}>
          Book Appointment
        </div>
      </div>

      <div className={style.groupcontact}>
        <div className={style.contact}>
          <div>
            <h1>Contact us</h1>
            <span>
              044-48557830/ 8015162649/ 7550187731 <br />{" "}
              healthpluscenter2016@gmail.com
            </span>
          </div>
          <div>
            <h1>Address</h1>
            <span>
              No 26, Sumeru square, Kanaka Nagar, <br /> Agaram Main road ,
              Selaiyur,
              <br />
              Chennai -600073
            </span>
          </div>
        </div>
        <div className={style.map}>
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3889.0473547178653!2d80.13993111482132!3d12.904676590899752!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zMTLCsDU0JzE2LjgiTiA4MMKwMDgnMzEuNiJF!5e0!3m2!1sen!2sin!4v1602968141843!5m2!1sen!2sin"
            frameborder="0"
            style={{ border: 0 }}
            allowfullscreen=""
            aria-hidden="false"
            tabindex="0"
          ></iframe>
        </div>
      </div>
    </div>
  );
};

export default About;
